.typewriterh1 {
  width: -moz-fit-content;
  width: fit-content;
  text-align: right;
  color: #c5fceb;
  white-space: nowrap;
  border-right: .15em solid orange;
  margin: 0 0 30px auto;
  font-size: 28px;
  animation: typing 3s steps(20, end), blink-caret .75s infinite;
  overflow: hidden;
}

@media (min-width: 768px) {
  .typewriterh1 {
    font-size: 50px;
  }
}

@keyframes typing {
  from {
    width: 0;
  }

  to {
    width: 100%;
  }
}

@keyframes blink-caret {
  0% {
    border-color: #0000;
  }

  50% {
    border-color: orange;
  }

  100% {
    border-color: #0000;
  }
}

h2 a {
  color: #c5fceb;
  opacity: 1;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

h2 a:hover {
  color: #fec24a;
}

h2 a:after {
  content: "";
  width: 100%;
  height: 1.5px;
  opacity: 1;
  background-color: #fec24a;
  animation: linkanimation .5s infinite alternate;
  position: absolute;
  bottom: 0;
  left: 0;
}

@keyframes linkanimation {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  100% {
    opacity: .7;
    transform: translateY(1px);
  }
}

footer {
