body {
  width: 98vw;
  height: 100vh;
  background-image: url("https://www.nasa.gov/sites/default/files/thumbnails/image/stsci-01gfnn3pwjmy4rqxkz585bc4qh.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

body header label {
  cursor: pointer;
  z-index: 4;
  margin: 25px;
  padding: 7px 10px;
  position: fixed;
}

body header label .bar {
  width: 30px;
  height: 3px;
  background-color: #fec24a;
  border-radius: 5px;
  margin: 5px auto;
  transition: background-color .5s ease-in, transform .5s ease-in, width .5s ease-in;
  display: block;
}

body header label .arrowtop, body header label .arrowbottom {
  opacity: 0;
  width: 15px;
  transition: all 1s;
  display: block;
  position: absolute;
  top: 23px;
}

body header label .arrowtop {
  transform: translate(100vw, -4px)rotate(25deg);
}

body header label .arrowbottom {
  transform: translate(100vw, -12px)rotate(-25deg);
}

@media (min-width: 768px) {
  body header label {
    margin: 50px;
    transform: scale(2);
  }
}

body header input[type="checkbox"] {
  display: none;
}

body header input[type="checkbox"]:checked ~ aside {
  left: 0%;
}

body header input[type="checkbox"]:checked ~ label .top {
  background-color: #c5fceb;
  transform: translateY(1rem)rotate(405deg);
}

body header input[type="checkbox"]:checked ~ label .bottom {
  background-color: #fec24a;
  transform: translateY(-1rem)rotate(315deg);
}

body header input[type="checkbox"]:checked ~ label .middle {
  background-color: #fec24a;
  transform: translate(calc(100vw - 65px))scaleX(.75);
}

body header input[type="checkbox"]:checked ~ label .arrowtop {
  opacity: 1;
  transform: translate(calc(100vw - 62px), -12px)rotate(-25deg);
}

body header input[type="checkbox"]:checked ~ label .arrowbottom {
  opacity: 1;
  transform: translate(calc(100vw - 62px), -4px)rotate(25deg);
}

@media (min-width: 600px) {
  body header input[type="checkbox"]:checked ~ label .middle {
    background-color: #fec24a;
    transform: translate(calc(100vw - 75px))scaleX(.75);
  }

  body header input[type="checkbox"]:checked ~ label .arrowtop {
    opacity: 1;
    transform: translate(calc(100vw - 72px), -12px)rotate(-25deg);
  }

  body header input[type="checkbox"]:checked ~ label .arrowbottom {
    opacity: 1;
    transform: translate(calc(100vw - 72px), -4px)rotate(25deg);
  }
}

@media (min-width: 768px) {
  body header input[type="checkbox"]:checked ~ label .arrowbottom, body header input[type="checkbox"]:checked ~ label .arrowtop {
    display: none;
  }
}

body header aside {
  opacity: .9;
  width: 40%;
  z-index: 3;
  background-color: #16213e;
  transition: all .4s ease-in-out;
  position: fixed;
  top: 0;
  bottom: 0;
  left: -40%;
}

body header aside .navbar {
  text-align: right;
  border-top: 2px solid #c5fceb;
  margin-top: 100px;
  padding: 0;
  list-style: none;
}

body header aside .navbar .navitem {
  color: #c5fceb;
  border-bottom: 2px solid #c5fceb;
  padding: 15px 10px;
  font-size: 20px;
  text-decoration: none;
  transition: color .3s ease-in .15s;
  display: block;
  position: relative;
}

body header aside .navbar .navitem:before {
  content: "";
  height: 100%;
  width: 0%;
  z-index: -1;
  background-color: #c5fceb;
  transition: width .3s ease-in;
  position: absolute;
  bottom: 0;
  left: 0;
}

body header aside .navbar .navitem:hover {
  color: #16213e;
}

body header aside .navbar .navitem:hover:before {
  width: 100%;
}

@media (min-width: 768px) {
  body header aside .navbar {
    margin-top: 150px;
  }

  body header aside .navbar .navitem {
    font-size: 35px;
  }
}

@media (min-width: 1024px) {
  body header aside {
    width: 25%;
    left: -25%;
  }
}

