.hole {
  opacity: 1;
  clip-path: circle(25%);
  height: 100%;
  animation: holefade 4s infinite;
  position: relative;
}

@keyframes holefade {
  0% {
    opacity: 0;
  }

  2% {
    opacity: 0;
    transition: all ease-in;
  }

  20% {
    opacity: 1;
  }

  70% {
    opacity: 1;
    transition: all ease-out;
  }

  98% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

.sky {
  z-index: 0;
  height: 60%;
  background-color: #87ceeb;
  position: absolute;
  inset: 0 0 40%;
}

.sun {
  z-index: 2;
  width: 34px;
  height: 34px;
  transform-origin: 17px 130px;
  background-color: #fff200;
  border-radius: 50%;
  animation: sunrise 4s infinite;
  position: absolute;
  top: 110px;
  left: 183px;
  transform: rotateZ(-90deg);
}

@keyframes sunrise {
  0% {
    transition: all;
    transform: rotate(-90deg);
  }

  35% {
    transition: all ease-in;
    transform: rotate(-25deg);
  }

  100% {
    transform: rotateZ(90deg);
  }
}

.sand {
  z-index: 3;
  background-color: #f7db86;
  position: absolute;
  inset: 60% 0 0;
}

.pyrleft {
  width: 100%;
  height: 400px;
  z-index: 2;
  clip-path: polygon(50% 45%, 35% 60%, 55% 60%);
  background-color: #fff;
  animation: shadowleft 4s infinite;
  position: absolute;
  top: 0;
}

@keyframes shadowleft {
  0% {
    background-color: #fff;
  }

  35% {
    background-color: #fff;
  }

  70% {
    background-color: #ccc;
  }

  100% {
    background-color: #ccc;
  }
}

.pyrright {
  width: 100%;
  height: 400px;
  z-index: 2;
  clip-path: polygon(50% 45%, 55% 60%, 65% 60%);
  background-color: #ccc;
  animation: shadowright 4s infinite;
  position: absolute;
  top: 0;
}

@keyframes shadowright {
  0% {
    background-color: #aaa;
  }

  40% {
    background-color: #ccc;
  }

  100% {
    background-color: #fff;
  }
}

.shadow {
  width: 100%;
  height: 400px;
  z-index: 3;
  clip-path: polygon(90% 60%, 35% 60%, 65% 60%);
  background-color: #c8b26e;
  animation: shadowmove 4s infinite;
  position: absolute;
  top: 0;
}

@keyframes shadowmove {
  0% {
    clip-path: polygon(90% 60%, 35% 60%, 65% 60%);
    transition: all ease-out;
  }

  35% {
    clip-path: polygon(73% 71%, 35% 60%, 65% 60%);
    transition: all ease-in;
  }

  50% {
    clip-path: polygon(50% 69%, 35% 60%, 65% 60%);
    transition: all ease-in;
  }

  70% {
    clip-path: polygon(28% 72%, 35% 60%, 65% 60%);
    transition: all ease-in;
  }

  95% {
    clip-path: polygon(19% 60%, 35% 60%, 65% 60%);
  }
}

