* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: absolute;
}

main {
  margin: 0px auto;
  width: 85vw;
  min-width: 300px;
  padding: 30px 0 100px 0;
}

:root {
  font-family: monospace;
}

.typewriterh1 {
  margin: 0 0 30px auto;
  width: fit-content;
  text-align: right;
  font-size: 28px;
  color: $secondary-color;
  overflow: hidden;
  border-right: 0.15em solid orange;
  white-space: nowrap;
  animation: typing 3s steps(20), blink-caret 0.75s infinite;
}

@media (min-width: 768px) {
  .typewriterh1 {
    font-size: 50px;
  }
}

/* The typing effect */
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  0% {
    border-color: transparent;
  }
  50% {
    border-color: orange;
  }
  100% {
    border-color: transparent;
  }
}

h2 a {
  color: $secondary-color;
  text-decoration: none;
  display: inline-block;
  position: relative;
  opacity: 1;
  text-align: center;

  &:hover {
    color: $accent-color;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0px;
    left: 0;
    width: 100%;
    height: 1.5px;
    background-color: $accent-color;
    opacity: 1;
    animation: linkanimation infinite 0.5s alternate;
  }
}

@keyframes linkanimation {
  0% {
    opacity: 1;
    transform: translateY(0px);
  }

  100% {
    opacity: 0.7;
    transform: translateY(1px);
  }
}

footer {
  z-index: 4;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  padding: 10px;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: $primary-color;
  font-size: 12px;
  color: $secondary-color;

  div {
    margin-left: 2rem;
  }

  a {
    padding: 0.5rem;
    color: $accent-color;
    font-size: 20px;
  }
}

@media (min-width: 768px) {
  footer {
    font-size: 20px;

    a {
      font-size: 30px;
    }
  }
}
